@font-face {
	font-family: "arcade-classic";
	font-style: normal;
	font-weight: normal;
	src: url("./static/ARCADECLASSIC.TTF") format("truetype");
}

body {
	overflow: hidden;

}
body::before {
	display: block;
	position: absolute;
	content: '';
	width: 100%;
	height: 100%;
	background-image: url('./static/7110.png');
	background-repeat: no-repeat;
	background-size: 80%;
	background-position-y: -200px;
	background-position-x: -200px;
}

#game {
	position: absolute;
	z-index: -1;
	top: 205px;
	left: 120px;
    border: solid 1px #e0e0e0;
    width: 750px;
    height: 750px;
}